.footer {
  background-color: #f1f1f1;
  padding: 40px 20px;
  text-align: center;
  border-top: 1px solid #ddd;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
}

.footer-content h3 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.footer-content p {
  margin: 8px 0;
  color: #666;
  font-size: 14px;
}

.socials {
  list-style: none;
  padding: 0;
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 16px;
}

.socials li a {
  color: #555;
  font-size: 18px;
  transition: color 0.3s ease;
}

.socials li a:hover {
  color: #007bff;
}

/* Updated horizontal layout for links */
.links {
  position: absolute;
 margin-left: 300px;bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  justify-content: center;
  gap: 40px;
  padding: 20px;
  flex-wrap: wrap;
}

.links ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
  list-style: none;
  padding: 0;
  margin: 0;
  flex-wrap: wrap;
}

.links ul li a {
  color: #555;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
  padding: 5px 10px;
}

.links ul li a:hover {
  color: #007bff;
  text-decoration: underline;
}

/* Copyright */
.footer + p {
  text-align: center;
  font-size: 13px;
  color: #999;
  margin: 20px 0;
}

