.cloths-container {
  padding: 20px;
  overflow-x: hidden;
}

.cloths-container h2 {
  text-align: center;
  margin-bottom: 10px;
}

.cloths-container p {
  text-align: center;
  color: #777;
  margin-bottom: 20px;
}

/* Flexbox container */
.cloths-list {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  gap: 20px;
  padding-bottom: 10px;
  scrollbar-width: none;
}

.cloths-list::-webkit-scrollbar {
  display: none;
}

/* Each cloth item */
.cloth-item {
  min-width: 180px;
  max-width: 180px;
  height: 270px;
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
  background-color: white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  padding: 10px;
}

.cloth-item img {
  width: 100%;
  height: 140px;
  object-fit: contain;
  border-radius: 6px;
  margin-bottom: 10px;
}

.cloth-item h3 {
  font-size: 16px;
  margin: 5px 0;
}

.cloth-item p {
  font-size: 14px;
  margin: 2px 0;
  color: #444;
}
